import { Injectable } from '@angular/core';
import { AdaptRxDatetimeAdapter, RxDatetimeStruct } from '@bmc-ux/adapt-angular';
import moment from 'moment-es6';

@Injectable()
export class DateAdapter extends AdaptRxDatetimeAdapter<string> {
  private storeDateFormat = 'YYYY-MM-DD'; // AR server only accepts this format for Date.

  fromModel(dateString: string): RxDatetimeStruct {
    let adaptedValue: RxDatetimeStruct = null;
    const date = moment(dateString);

    if (date && date.isValid()) {
      adaptedValue = {
        year: date.year(),
        month: date.month(),
        date: date.date(),
        hours: date.hours(),
        minutes: date.minutes(),
        seconds: date.seconds()
      };
    }

    return adaptedValue;
  }

  toModel(date: RxDatetimeStruct): string {
    return date
      ? date.parseError
        ? moment.invalid().toString()
        : moment([date.year || 0, date.month || 0, date.date || 0]).format(this.storeDateFormat)
      : null;
  }
}
